<template>
  <div>
   <cms-editor v-model="txt" @ready='getEditor' :config='options'></cms-editor>
    <div class="wx-editor__toolbar">
      <div class="wx-emotion">
        <span class="wx-emotion__tag" @click="visable=true">
          <img src="./../../../../../assets/images/emoji.png" alt="">
        </span>
        <ul class="wx-emotion__items" v-show="visable">
          <li class="wx-emotion__item" v-for="(val,key,index) in emojiList" :key="index"
           :data-value='key'
           @click="addEmoji(val,key)">
            <i class="emoji" :class="'emoji'+val" ></i>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { emojiItems } from './../emoji.js'

export default {
  data() {
    return {
      options: { toolbars: [],
        enableContextMenu: false,
        maximumWords: 600,
        enableAutoSave: false,
        saveInterval: 0,
        elementPathEnabled: false,
        imageScaleEnabled: false,
        iframeCssUrl: '/static/css/emoji.css'
      },
      txt: '',
      emojiList: emojiItems(),
      ue: null,
      visable: false
    }
  },
  watch: {
    txt() {
      this.$emit('change', this.replaceEmoji(this.ue.getContent()))
    },
    visable(value) {
      if (value) {
        window.addEventListener('click', this.closeMenu)
      } else {
        window.removeEventListener('click', this.closeMenu)
      }
    }
  },
  methods: {
    test() {

    },
    getEditor(ue) {
      this.ue = ue
    },
    addEmoji(val, key) {
      const obj = `<img class='emoji emoji${val}' data-value='${key}'/>`
      this.ue.execCommand('insertHtml', obj)
    },
    closeMenu() {
      this.visable = false
    },
    replaceEmoji(str) {
      // emoji占位符
      var placeholder = '${emoji}'
      var placeholderLen = placeholder.length
      // 定义匹配img 标签属性正则表达式
      var imgReg = /<img.*?(?:>|\/>)/gi
      // 匹配获取所有img标签属性，返回数组
      var arr = str.match(imgReg)
      if (arr === null) {
        return str.replace(/<\/?.+?>/g, '')
      }
      // 替换img标签为指定占位符
      str = str.replace(/<img\/?.+?>/g, placeholder)
      // 替换所有html标签
      str = str.replace(/<\/?.+?>/g, '')
      // 定义匹配data-valu属性正则表达式
      var srcReg = /data-value=[\'\"]?([^\'\"]*)[\'\"]?/i
      // 循环替换所有占位符为对应data-value属性值
      for (var i = 0; i < arr.length; i++) {
        const data_value = arr[i].match(srcReg)
        let emojistr = ''
        if (data_value != null) {
          emojistr = data_value[1]
        }
        var firstIndex = str.indexOf(placeholder)
        str = this.replacePos(str, firstIndex, placeholderLen, emojistr)
      }
      return str
    },
    /**
	*指定位置替换指定字符
	* str  替换原字符
	* pos 起始替换位置
	* len  起始替换位置后长度
	* replacetext  指定替换字符
	*/
    replacePos(str, pos, len, replacetext) {
      str = str.substr(0, pos) + replacetext + str.substring(pos + len, str.length)
      return str
    }
  },
  mounted() {}
}
</script>

<style >
</style>



