<template>
  <div id="sendLoading">
    <el-tabs v-model="activeName" type="border-card" class="visable"  style="margin:25px 0;">
      <el-tab-pane label="图文消息" name="mpnews">
        <!-- 未选择图文消息 -->
        <div class="wx-create" v-if="news.length<= 0">
          <image-text-zone class="wx-create__item" @change="getNews">
            <div class="wx-create__item">
              <p class="t-icon"><img src="./../../../../../assets/images/icon-imgTxt.png" alt=""></p>
              <p class="wx-create__title">从素材库选择</p>
            </div>
          </image-text-zone>
          <div class="wx-create__item" @click="$routerLink('/wechatMaterial/imageText/save','save')">
            <p class="iconfont icon-jiaoxueneirong_zhongmingming"></p>
            <p class="wx-create__title">新建图文</p>
          </div>
        </div>
        <!--回填显示图文-->
        <div v-else class="wx-create__inner" style="width:340px;">
          <div class="we-card__warpper" v-for="(content) in news" :key="content.id">
            <template v-for="(item,index) in content.requestArray">
              <div class="we-card__item we-card__item--thumb" v-if="index==0" :key="index">
                <a :href="item.url" target="_blank" class="we-card__link">
                  <img class="we-card__img" :src="$cms.weChatUrl(item.thumbMediaUrl)" alt="">
                  <span class="we-card__title--thumb" v-if="item.title!=''">
                    {{item.title}}
                  </span>
                </a>
                <div class="we-card-mask__preview">
                  <a :href="item.url" target="_blank">预览文章</a>
                </div>
              </div>
              <div class="we-card__item  we-card__item--sub" v-else :key="index">
                <a :href="item.url" target="_blank" class="we-card__link">
                  <div class="we-card__sub--inner">
                    <div class="we-card__sub--img">
                      <img :src="$cms.weChatUrl(item.thumbMediaUrl)" alt="">
                    </div>
                    <div class="we-card__sub--title">
                      {{item.title}}
                    </div>
                  </div>
                </a>
                <div class="we-card-mask__preview">
                  <a :href="item.url" target="_blank">预览文章</a>
                </div>
              </div>
            </template>
            <div class="we-card__footer">
              <span class="fl">
                更新时间 {{$cms.getDateDiff(content.wechatUpdateTime*1000)}}
              </span>
              <span class="fr">
                <i class="el-icon-delete" @click="news=[]"></i>
              </span>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="文字" name="text" >
          <editor @change="getText"></editor>
      </el-tab-pane>
      <el-tab-pane label="图片消息" name="image">
        <div class="wx-create" v-if="images.length<= 0">
          <image-zone class="wx-create__item" @change="getImages">
            <div class="wx-create__item">
              <p class="t-icon"><img src="./../../../../../assets/images/icon-img.png" alt=""/></p>
              <p class="wx-create__title">从素材库选择图片</p>
            </div>
          </image-zone>
        </div>
        <div class="wx-create__inner" v-else style="width:340px;">
          <div class="relative">
            <img :src="$cms.weChatUrl(images[0].request.url)" alt="" class="wx-create__img">
            <image-zone class="wx-repalce__warpper" @change="getImages">
              <div class="wx-repalce__btn">替换素材</div>
            </image-zone>
          </div>
        </div>
      </el-tab-pane>
      <!-- 语音 -->
      <el-tab-pane label="语音消息" name="voice">
         <div class="wx-create" v-if="voices.length<= 0">
          <audio-zone class="wx-create__item" @change="getVoice">
            <div class="wx-create__item">
              <p class="t-icon"><img src="./../../../../../assets/images/icon-voice.png" alt=""></p>
              <p class="wx-create__title">从素材库选择音频</p>
            </div>
          </audio-zone>
        </div>
        <div class="wx-create__inner" v-else style="width:340px;">
          <div class="relative">
             <div class="preview-audio__context">
               <div class="preview-audio__player"></div>
               <div class="preview-audio__name">
                  {{voices[0].request.name}}
               </div>
             </div>
            <audio-zone class="wx-repalce__warpper" @change="getVoice">
              <div class="wx-repalce__btn">替换素材</div>
            </audio-zone>
          </div>
        </div>
      </el-tab-pane>
      <!-- 视频消息 -->
      <el-tab-pane label="视频消息" name="mpvideo">
          <div class="wx-create" v-if="videos.length<= 0">
          <video-zone class="wx-create__item" @change="getVideo">
            <div class="wx-create__item">
              <p class="t-icon"><img src="./../../../../../assets/images/icon-video.png" alt=""></p>
              <p class="wx-create__title">从素材库选择视频</p>
            </div>
          </video-zone>
        </div>
        <div class="wx-create__inner" v-else style="width:340px;">
          <div class="relative">
             <div class="preview-audio__context">
               <div class="preview-audio__player"></div>
               <div class="preview-audio__name">
                  {{videos[0].request.name}}
               </div>
             </div>
            <video-zone class="wx-repalce__warpper" @change="getVoice">
              <div class="wx-repalce__btn">替换素材</div>
            </video-zone>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <div class="center">
      <el-button @click="sendMessage" type="primary">
        发送
      </el-button>
    </div>
  </div>
</template>

<script>
// 图文组件
import imageTextZone from './../../materialLibrary/components/wxImageTextZone'
import imageZone from './../../materialLibrary/components/wxPictureZone'
import audioZone from './../../materialLibrary/components/wxAudioZone'
import videoZone from './../../materialLibrary/components/wxVideoZone'
import editor from './editor'
import { weChatFansSend } from './../../../../../api/wechat/index.js'
export default {
  name: 'fans-editor',
  components: {
    imageTextZone,
    imageZone,
    audioZone,
    videoZone, editor
  },
  props: {
    openId: {
      type: String
    }
  },
  data() {
    return {
      activeName: 'mpnews',
      news: [], // 显示的图文
      images: [],
      videos: [],
      voices: [],
      text: ''
    }
  },
  methods: {
    getNews(items) {
      // 获取图文
      this.news = items
    },
    getImages(items) {
      this.images = items
    },
    getVoice(items) {
      this.voices = items
    },
    getVideo(items) {
      this.videos = items
    },
    getText(txt) {
      this.text = txt
    },
    sendMessage() {
      // 发送消息
      const params = {
        touser: this.openId,
        msgtype: '',
        content: '',
        media_id: ''
      }
      if (this.activeName === 'mpnews' && this.news.length > 0) {
        // 图文
        params.msgtype = 'mpnews'
        params.media_id = this.news[0].mediaId
      }
      if (this.activeName === 'image' && this.images.length > 0) {
        // image
        params.msgtype = 'image'
        params.media_id = this.images[0].mediaId
      }
      if (this.activeName === 'mpvideo' && this.videos.length > 0) {
        // video
        params.msgtype = 'mpvideo'
        params.media_id = this.videos[0].mediaId
      }
      if (this.activeName === 'voice' && this.voices.length > 0) {
        // audio
        params.msgtype = 'voice'
        params.media_id = this.voices[0].mediaId
      }
      if (this.activeName === 'text' && this.text !== '') {
        // audio
        params.msgtype = 'text'
        params.media_id = ''
        params.content = this.text
      }
      if (params.msgtype !== '') {
        const loading = this.$loading({
          target: '#sendLoading',
          text: '正在发送...'
        })
        weChatFansSend(params).then(res => {
          loading.close()
          if (res.code === 200) {
            this.$successMessage('发送成功')
          } else {
            this.$errorMessage(res.message)
          }
        })
      } else {
        this.$errorMessage('请选择发送的内容')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .t-icon{
    height: 30px;
    img{
      max-height: 100%;
    }
  }
  .t-icon2{
    height: 25px;
    img{
      max-height: 100%;
    }
  }
  .wx-create__title{
    font-size: 14px;
    color: #666;
  }
</style>



